$red: #e01c34;
$white: #ffffff;
$light: #f1f1f1;
$gray: #888;

$tb-border-color: #ddd;

html,
body {
	font-size: 20px;
	height: 100%;
}
ul{
	padding-left: 0;
}
p {
	margin: 12px 0;
}
hr{
	border: 0;
    height: 1px;
    background-color: #ccc;
}
.text-gray {
	color: $gray;
}
.text-white {
	color: $white;
}
.text-red {
	color: $red;
}

.fl-left {
	float: left;
}
.fl-right {
	float: right;
}
.font-14 {
	font-size: 14px;
}
.font-16 {
	font-size: 16px;
}
.font-24 {
	font-size: 24px;
}
.pl-10 {
	padding-left: 10px;
}
.pr-10 {
	padding-right: 10px;
}
.px-10 {
	padding-left: 10px;
	padding-right: 10px;
}
.mt-10 {
	margin-top: 10px;
}
.mb-10 {
	margin-bottom: 10px;
}
.mr-10 {
	margin-right: 10px;
}
.ml-10 {
	margin-left: 10px;
}
.mx-10 {
	margin-left: 10px;
	margin-right: 10px;
}
.my-10 {
	margin-top: 10px;
	margin-bottom: 10px;
}
.m-10{
	margin: 10px;
}
.mx-auto {
	margin-left: auto;
	margin-right: auto;
}
.text-center {
	text-align: center;
}

.prefix {
	display: inline-block;
	height: 0.8em;
	margin: 0 0.6em;
	border-left: 3px solid $red;
	vertical-align: middle;
}

ons-back-button svg {
	height: 14px;
}
ons-back-button .back-button__label {
	font-size: 14px;
}


/** 
* table with scroll wrapper  
*/
.tb-wrapper {
    display: block;
	width: 100%;
    overflow-x: scroll;
    -webkit-overflow-x-scrolling: touch;
    -ms-overflow-x-style: none;
    -ms-scroll-snap-type: mandatory;

	> .tb {
		table-layout: fixed;
		min-width: 100%;
		border: 1px solid $tb-border-color;
		border-collapse: collapse;
        
		> thead {
		    > tr {
			background-color: $gray;
			color: $white;
		    }
		}

		> tbody {
		    > tr {
			&:nth-of-type(odd) {
			    background-color: $white;
			}
			&:nth-of-type(even) {
			    background-color: $light;
			}
		    }
		}

		th, td{
		    font-size: 16px;
		    padding: 0.5em;
		    white-space: nowrap;
		    vertical-align: middle;
		    border: 1px solid $tb-border-color;
		    font-weight: 300;
		    text-align: center;
		}

		th {
		    padding: 0.5em 2em;
		    font-weight: normal;
		}
	}
}

/* list abnormal */
.list{
	&.abnormal{
		background: $red;
		*{
			color: $white;
		}
	}
}